import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';

var app = getApp().globalData
export default {
	
	data() {
		return {
			myProps: {
			  label: 'name',
			  value: 'name',
			  children: 'child'
			},
			triggered: false,
			tabindex:0,
			tablist:['企业',"服务"],
			voteList:[],
			votetop:false,
			voteallowtop:true,
			votebottom:false,
			voteallowbottom:false,
			voteId:'',
			historyList:[],
			historyId:'',
			historytop:false,
			historyallowtop:true,
			historybottom:false,
			historyallowbottom:false,
			id:'',
			page1:1,
			limit:10,
			city1:'',
			search1:'',
			cate_id:'',
			List2:[],
			page2:1,
			showTitle:false,
			showTitle1:false,
			city:'全部',
			freshing:false,
			triggered1: false,
			freshing1:false,
			list:[],
			showtext:true,
			attention_id:0,
			collect_id:0
			
		}
	},
	onLoad(e) {
	},
	onShow() {
		this.attention_id = 0
		this.collect_id = 0
		uni.showLoading({
		    title: '加载中'
		});
		// 企业
		this.getVote()
		// 服务
		this.getHistory()
	},
	methods: {
		// 长按取消收藏服务
		longF(item1){
			// console.log(item)
			if(uni.getStorageSync('userId')){
				Dialog.confirm({
				  title: '提示',
				  message: '确定取消收藏该条服务吗？'
				}).then(() => {
					this.$api.collect_sp({
						token: uni.getStorageSync("token"),
						user_id: uni.getStorageSync("userId"),
						type:0,
						id:item1.id
					}).then(res=>{
						if(res.data.code=="200"){
							uni.showToast({
							    title: '取消收藏服务成功',
							    duration: 2000,
								icon:'none'
							});
							this.historyList = this.historyList.filter((item,index)=> item.id != item1.id)
						}else{
							uni.showToast({
							    title: res.data.msg,
							    duration: 2000,
								icon:'none'
							});
						}
					})
					})
			}else{
				uni.clearStorageSync()
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				uni.reLaunch({
					url: '/pages/login/login'
				})
			}
		},
		// 长按取消   关注企业
		longC(item1){
			// console.log(item)
			if(uni.getStorageSync('userId')){
				Dialog.confirm({
				  title: '提示',
				  message: '确定取消关注该企业吗？'
				}).then(() => {
					this.$api.attention_company({
						token: uni.getStorageSync("token"),
						user_id: uni.getStorageSync("userId"),
						type:0,
						id:item1.id
					}).then(res=>{
						if(res.data.code=="200"){
							uni.showToast({
							    title: '取消关注企业成功',
							    duration: 2000,
								icon:'none'
							});
							this.voteList = this.voteList.filter((item,index)=> item.id != item1.id)
						}else{
							uni.showToast({
							    title: res.data.msg,
							    duration: 2000,
								icon:'none'
							});
						}
					})
										
				}).catch(() => {
				  // on cancel
				});
				// 0就是没关注要关注
					
				
			}else{
				uni.clearStorageSync()
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				uni.reLaunch({
					url: '/pages/login/login'
				})
			}
		},
		company(item){
			uni.navigateTo({
				url:'/pagesTwo/MyBusiness/MyBusiness?id='+item.id
			})
		},
		serviceDetails(item){
			uni.navigateTo({
				url:'/pagesFour/ServiceDetails/ServiceDetails?id='+item.id +'&status=0'+"&user_id=" + item.user_id
			})
		},
		bottomvote(){
			// 关注的企业
			if(this.voteallowbottom){
				this.votebottom = true
				this.voteallowbottom = false
				this.attention_id = this.voteList[this.voteList.length-1].attention_id
				setTimeout(()=> {
					this.getVote()
				}, 100);
				
			}
		},
		bottomhis(){
			// 收藏的服务
			if(this.historyallowbottom){
				this.historybottom = true
				this.historyallowbottom = false
				this.collect_id = this.historyList[this.historyList.length-1].collect_id
				setTimeout(()=> {
					this.getHistory()
				}, 100);
				
			}
		},	
		onPulling(e) {
			this.triggered = true;
		},
		onRefresh() {
			if (this.freshing) return;
			this.freshing = true;
			this.attention_id = 0
			this.collect_id = 0
			this.getHistory()
			this.getVote()
			setTimeout(() => {
				this.triggered = false;
				this.freshing = false;
				uni.showToast({
				    title: '刷新成功',
				    mask: "true",
					icon:'none'
				});
			},2000)
		},
		onRestore() {
			this.triggered = false; // 需要重置
		},
		onAbort() {
			// this.triggered = false;
		},
		onPulling1(e) {
			this.triggered1 = true;
		},
		onRefresh1() {
			if (this.freshing1) return;
			this.freshing1 = true;
			this.attention_id = 0
			this.collect_id = 0
			this.getHistory()
			this.getVote()
			setTimeout(() => {
				this.triggered1 = false;
				this.freshing1 = false;
				uni.showToast({
				    title: '刷新成功',
				    mask: "true",
					icon:'none'
				});
			},2000)
		},
		onRestore1() {
			this.triggered1 = false; // 需要重置
		},
		// 详情
		InformationDetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InformationDetail/InformationDetail?id='+id
			})
		},
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 关注的企业
		getVote(){
			this.$api.my_attention_company({
				attention_id:this.attention_id,
				limit:20,
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				// console.log(res)
				uni.hideLoading();
				if(!this.voteallowtop){
					setTimeout(()=>{
						uni.showToast({
						    title: '刷新成功',
						    duration: 1000,
							icon:'none'
						});
						this.votetop = false
						this.voteallowtop = true
					}, 1000);
				}
				if(!this.attention_id){
					if(res.data.data.length>=20){
						this.voteList = res.data.data
						this.voteallowbottom = true
						this.showTitle = false
						
					}else{
						this.voteList = res.data.data
						this.voteallowbottom = false
						this.showTitle = true
					}														
				}else{
					
					if(res.data.data.length>=20){
						this.voteList = this.voteList.concat(res.data.data)
						// console.log(this.voteList)
						this.voteallowbottom = true
						this.showTitle = false
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.votebottom = false
						}, 1000);
						 
					}else{
						this.voteList = this.voteList.concat(res.data.data)
						this.voteallowbottom = false
						this.showTitle = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.votebottom = false
						}, 1000);
					}
				}
			})
		},
		// 服务列表
		getHistory(){
			this.$api.my_collect_sp({
				collect_id:this.collect_id,
				limit:20,
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				// console.log(res)
				if(!this.historyallowtop){
					setTimeout(()=>{
						uni.showToast({
						    title: '刷新成功',
						    duration: 1000,
							icon:'none'
						});
						this.historytop = false
						this.historyallowtop = true
					}, 1000);
				}
				if(!this.collect_id){
					// console.log(1)
					if(res.data.data.length>=20){
						this.historyList = res.data.data
						this.historyallowbottom = true
						this.showTitle1 = false
						
					}else{
						this.historyList = res.data.data
						this.historyallowbottom = false
						this.showTitle1 = true
					}														
				}else{
					// console.log(2)
					if(res.data.data.length>=20){
						this.historyList = this.historyList.concat(res.data.data)
						this.showTitle1 = false
						this.historyallowbottom = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.historybottom = false
						}, 1000);
						 
					}else{
						this.historyList = this.historyList.concat(res.data.data)
						this.historyallowbottom = false
						this.showTitle1 = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.historybottom = false
						}, 1000);
						
					}
					
					
				}
			})
		},
		changeSwiper(e){
			this.tabindex = e.detail.current
		},
		tab(ind){
			this.tabindex = ind
		},
		
		
	}
}
